.item {
    padding: 10px;
    margin: 10px auto;
    border: 2px solid rgb(59, 43, 91);
    border-radius: 7px;
    box-sizing: border-box;
    width: 60%;
    text-align: center;
    position: relative;
    -webkit-transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.item::after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
.item:hover{
    box-shadow: 0 20px 50px rgba(59, 43, 91, 0.7);
    -webkit-transform: scale(1.01, 1.01);
    transform: scale(1.011, 1.011);
}
.item:hover::after {
    opacity: 1;
}
